import app from 'flarum/forum/app';
import Component from 'flarum/common/Component';
import extractText from 'flarum/common/utils/extractText';
import ItemList from 'flarum/common/utils/ItemList';
import abbreviateNumber from 'flarum/common/utils/abbreviateNumber';
/**
 * The `ImpactStats` component displays key metrics about Flarum's impact
 * and community support.
 */
export default class ImpactStats extends Component {
  view(vnode) {
    const stats = this.statsItems();
    if (stats.isEmpty()) {
      return null;
    }
    return m("div", {
      className: "ImpactStats"
    }, m("div", {
      className: "container"
    }, m("div", {
      className: "ImpactStats-grid"
    }, stats.toArray())));
  }
  statsItems() {
    const items = new ItemList();

    // Supporters count
    const supportersCount = app.forum.attribute('totalSupporters') || 0;
    items.add('supporters', this.statItem({
      icon: 'fas fa-users',
      value: supportersCount,
      label: extractText(app.translator.trans('flarum-discuss.forum.supporters.stats.supporters'))
    }), 100);

    // GitHub Stars
    const githubStars = app.forum.attribute('githubStars') || 0;
    items.add('github-stars', this.statItem({
      icon: 'fab fa-github',
      value: githubStars,
      label: extractText(app.translator.trans('flarum-discuss.forum.supporters.stats.github_stars'))
    }), 90);

    // Framework Commits
    const frameworkCommits = app.forum.attribute('frameworkCommits') || 0;
    items.add('framework-commits', this.statItem({
      icon: 'fas fa-code-branch',
      value: frameworkCommits,
      label: extractText(app.translator.trans('flarum-discuss.forum.supporters.stats.framework_commits'))
    }), 80);

    // Framework Contributors
    const frameworkContributors = app.forum.attribute('frameworkContributors') || 0;
    items.add('framework-contributors', this.statItem({
      icon: 'fas fa-user-friends',
      value: frameworkContributors,
      label: extractText(app.translator.trans('flarum-discuss.forum.supporters.stats.framework_contributors'))
    }), 70);
    return items;
  }
  statItem(stat) {
    return m("div", {
      className: "ImpactStat"
    }, m("div", {
      className: "ImpactStat-icon"
    }, m("i", {
      className: stat.icon,
      "aria-hidden": "true"
    })), m("div", {
      className: "ImpactStat-content"
    }, m("div", {
      className: "ImpactStat-value"
    }, abbreviateNumber(stat.value)), m("div", {
      className: "ImpactStat-label"
    }, stat.label)));
  }
}
flarum.reg.add('flarum-discuss', 'forum/components/ImpactStats', ImpactStats);