import Page from 'flarum/common/components/Page';
import PageStructure from 'flarum/forum/components/PageStructure';
import IndexSidebar from 'flarum/forum/components/IndexSidebar';
/**
 * BaseInfoPage - A reusable base component for informational pages
 *
 * Provides:
 * - Consistent hero section with SVG background
 * - Standard page structure with sidebar
 * - Reusable hero styling
 *
 * Usage: Extend this class and implement contentItems() to add page content
 */
export default class BaseInfoPage extends Page {
  view() {
    return m(PageStructure, {
      className: "".concat(this.pageClass(), " InfoPage Page--vertical"),
      hero: this.hero.bind(this),
      sidebar: this.sidebar.bind(this)
    }, this.contentItems().toArray());
  }

  /**
   * Override this to provide page-specific CSS class
   */

  /**
   * Override this to provide page content
   */

  /**
   * Hero section with gradient background
   */
  hero() {
    const title = this.heroTitle();
    const subtitle = this.heroSubtitle();
    const ctaButton = this.heroCta();
    return m("header", {
      className: "InfoPage-hero Hero"
    }, m("div", {
      className: "container"
    }, m("div", {
      className: "InfoPage-heroContent"
    }, m("h1", {
      className: "InfoPage-heroTitle"
    }, title), subtitle && m("p", {
      className: "InfoPage-heroSubtitle"
    }, subtitle), ctaButton)));
  }

  /**
   * Override to provide custom hero title
   */

  /**
   * Override to provide custom hero subtitle (optional)
   */
  heroSubtitle() {
    return null;
  }

  /**
   * Override to provide custom hero CTA button (optional)
   */
  heroCta() {
    return null;
  }

  /**
   * Standard sidebar
   */
  sidebar() {
    return m(IndexSidebar, null);
  }
}
flarum.reg.add('flarum-discuss', 'forum/components/BaseInfoPage', BaseInfoPage);